import React from 'react'; 
import { Container, Row, Col } from 'react-bootstrap';
import ReactImageMagnify from 'react-image-magnify';
import paintings from '../json/paintings.json'; 
import sculptures from '../json/sculptures.json';
import worksOnPaper from '../json/paintings.json';
import { Link, withRouter } from 'react-router-dom'; 
import Base from './base'; 
import "./ImageView.css"

class ImageView extends React.Component {
  
  constructor(props) {
    super(props);
    
    let currentImageIndex;

    let images = paintings; 
    if (props.imageType === "sculptures") {
      images = sculptures;
    }
    else if (props.imageType === "worksonpaper") {
      images = worksOnPaper; 
    }

    for (let index in images) {
      if ( images[index].id === this.props.match.params.id ) {
        currentImageIndex = Number(index);
      }
    }

    this.state = {
      currentImageIndex: currentImageIndex,
      currentImageId: this.props.match.params.id,
      showImageZoom: false, 
      images: images,
      showSocialIcons: false
    }
  }
 
  backToGallery = () => {
    this.props.history.push(`/${this.props.imageType}/`);  
  }

  nextPhoto = () => {
    var nextPhoto = this.nextPhotoPos();  
    this.setState({currentImageIndex: nextPhoto}); 
  }

  nextPhotoPos = () => {
    var numPhotos = this.state.images.length;
    var nextPhoto = (this.state.currentImageIndex + 1) % numPhotos;
    return nextPhoto; 
  }

  nextPhotoId = () => {
    var nextPhoto = this.nextPhotoPos();
    let nextRoute = `/${this.props.imageType}/${this.state.images[nextPhoto].id}`;
    return nextRoute;
  }

  previousPhotoPos() {
    var numPhotos = this.state.images.length;
    var nextPhoto = (this.state.currentImageIndex - 1);
    if (nextPhoto < 0) {
      nextPhoto = numPhotos - 1; 
    }
    return nextPhoto; 
  }
  
  previousPhotoId = () => {
    var nextPhoto = this.previousPhotoPos(); 
    return `/${this.props.imageType}/${this.state.images[nextPhoto].id}`; 
  }

  previousPhoto = () => {
    var nextPhoto = this.previousPhotoPos(); 
    this.setState({currentImageIndex: nextPhoto}); 
  }

  openPhotoZoom = () => {
    this.setState({showImageZoom:true});
  }

  closePhotoZoom = () => {
    this.setState({showImageZoom:false});
  }

  showSocialIcons = () => {
    this.setState({showSocialIcons:true}); 
  }

  hideSocialIcons = () => {
    this.setState({showSocialIcons:false}); 
  }

  render() {
    let socialIcons = 
      <span id="socialicons">
        <a href={"https://www.facebook.com/sharer/sharer.php?u=" + encodeURI(window.location.href)} target="_blank"><img src="/img/icons/facebook.svg" alt="facebook"/></a> 
        <a href={"https://www.twitter.com/intent/tweet?url=" + encodeURI(window.location.href)} target="_blank"><img src="/img/icons/twitter.svg" alt="twitter"/></a>
        <a href={"https://www.tumblr.com/widgets/share/tool?canonicalUrl=" + encodeURI(window.location.href)} target="_blank"><img src="/img/icons/tumblr.svg" alt="tumblr"/></a>
        <a href={"https://pinterest.com/pin/create/link/?url=" + encodeURI(window.location.href)} target="_blank"><img src="/img/icons/pinterest.svg" alt="pinterest"/></a>
      </span>;

    if (!this.state.showSocialIcons) {
      socialIcons = ""
    }

    const socialBar = 
      <span id="share" onMouseLeave={this.hideSocialIcons}>
        <span id="shareicon" onMouseEnter={this.showSocialIcons}>
          <img src="/img/icons/share.png"  height="30px" alt="share"/>
        </span>
        {socialIcons}
      </span> 

    const topBarImage = 
      <table width="100%" height="40px">
        <tbody>
          <tr>
            <td style={{textAlign:"left"}} width="15%">
              {socialBar}
            </td>
            <td style={{textAlign:"center"}} className="subheader" width="75%"></td>
            <td style={{textAlign:"right"}} width="10%"><img className="icon" src="/img/icons/info_icon.png" onClick={this.openPhotoZoom} /><img className="icon" src="/img/icons/galleryicon.png" onClick={this.backToGallery} alt="back to gallery"/></td>
          </tr>
        </tbody>
      </table> ;

    const topBarZoom = 
      <table width="100%" height="40px">
        <tbody>
          <tr>
            <td style={{textAlign:"left"}} width="15%">
              {socialBar}
            </td>
            <td style={{textAlign:"center"}} className="subheader" width="75%">{this.state.images[this.state.currentImageIndex].name}</td>
            <td style={{textAlign:"right"}} width="10%"><img className="icon" src="/img/icons/info_icon_grey.png" onClick={this.closePhotoZoom} /><img className="icon" src="/img/icons/galleryicon.png" onClick={this.backToGallery} alt="back to gallery"/></td>
          </tr>
        </tbody>
      </table> ;

    const zoomView = <div>
    {topBarZoom}
    <div style={{display: "flex", justifyContent: "center",  alignItems: "center", height: "100%"}}>
      <Container style={{width: "100%"}}>
        <Row>
          <Col xs={1} sm={1} md={1} lg={1} xl={1}>
            <Link to={this.previousPhotoId()}>
              <img src="/img/icons/baseline-arrow_back_ios-24px.svg" width="60px" style={{cursor:"pointer", top: "50%", position: "absolute", left:"50%", marginLeft:"-40px"}} onClick={this.previousPhoto} alt="previous"/>
            </Link>
          </Col>
          <Col xs={10} sm={10} md={10} lg={10} xl={10}>
            <div style={{height: "600px", textAlign: "center"}}>
            <table style={{width: "100%"}}>
              <tbody>
                <tr>
                  <td style={{width: "50%", padding: "10px"}}>
                <ReactImageMagnify {...{
                  smallImage: {
                    alt: this.state.images[this.state.currentImageIndex].name, 
                    src: this.state.images[this.state.currentImageIndex].src,
                    srcSet: this.state.images[this.state.currentImageIndex].srcset,
                    isFluidWidth: true,
                    sizes: '(max-width: 480px) 70vw, (max-width: 1000px) 40vw, 400px'
                  }, 
                  largeImage: {
                    alt: this.state.images[this.state.currentImageIndex].name, 
                    src: this.state.images[this.state.currentImageIndex].src,
                    width: this.state.images[this.state.currentImageIndex].width,
                    height: this.state.images[this.state.currentImageIndex].height
                  }, 
                  enlargedImagePortalId: "test" 
                  
                }} />
                </td>
                <td>
                  <span id="test" />
                </td>
                </tr>
                </tbody>
              </table>
            </div>
          </Col>
          <Col xs={1} sm={1} md={1} lg={1} xl={1}>
            <Link to={this.nextPhotoId()}>
              <img src="/img/icons/baseline-arrow_back_ios-24px.svg" width="60px" style={{cursor:"pointer", display: "block", transform: "scale(-1,1)", position: "absolute", top: "50%", left: "50%"}} alt="next" onClick={this.nextPhoto}/>
            </Link>
          </Col>
        </Row>
      </Container>
    </div>
    </div> ; 

    const photoView = 
    <div>
      {topBarImage}
      <div className="imageView">
        <img className="imageViewImage" src={this.state.images[this.state.currentImageIndex].src} srcSet={this.state.images[this.state.currentImageIndex].srcset} sizes="(max-width: 480px) 70vw, (max-width: 1000px) 40vw, 400px" />
      </div>
    </div>;

    let pageContent = photoView; 
    if (this.state.showImageZoom) {
      pageContent = zoomView; 
    }
    return (
      <Base content={pageContent} />
    );
  }
}

export default withRouter(ImageView);
